import React from 'react';
import { inject, observer } from 'mobx-react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    pokemonList: {
        width: '60vw',
        margin: 'auto',
        marginTop: '2vh',
        display: 'grid',
        gridTemplate: 'minmax(100px, 1fr) / repeat(auto-fit, minmax(180px, 1fr))',
    },
    pokemon: {
        margin: 'auto',
        alignContent: 'center',
        textAlign: 'center',
        display: 'grid',
        padding: '3%',
        background: 'rgb(139, 139, 139)',
        width: '80%',
        marginBottom: '10px',
        '&:hover': {
            background: 'rgb(54, 54, 54)',
            color: 'white',
        },
    },
    imagePokemon: {
        margin: 'auto',
    },
    modalWindow: {
        width: '75vw',
        zIndex: '20',
        position: 'absolute',
        top: '10vh',
        left: '12.5vw',
        background: 'white',
    },
    modal: {
        display: 'none',
    },
    gridContainer: {
        display: 'grid',
        border: '2px solid rgb(54, 54, 54)',
        alignItems: 'center',
        gridTemplateAreas: '"nameOfPokemon nameOfPokemon nameOfPokemon" "photo stat type1" "photo speed type1" "photo specialStats type2" "photo defenseAttack type2" "photo hp type3"',
    },
    nameOfPokemon: {
        padding: '0',
        gridArea: 'nameOfPokemon',
        textAlign: 'center',
        textTransform: 'uppercase',
        fontSize: '24pt',
        paddingLeft: '10%',
    },
    photo: {
        gridArea: 'photo',
        '&:first-child': {
            width: '90%',
            height: '90%',
            imageResolution: 'cover',
            margin: '5%',
            paddingLeft: '0',
        },
    },
    stat: {
        gridArea: 'stat',
        fontSize: '18pt',
        borderBottom: '3px solid rgb(54, 54, 54)',
    },
    speed: {
        gridArea: 'speed',
        borderBottom: '3px solid rgb(54, 54, 54)',
    },
    specialStats: {
        gridArea: 'specialStats',
        borderBottom: '3px solid rgb(54, 54, 54)',
    },
    hp: {
        gridArea: 'hp',
        paddingBottom: '20px',
    },
    type1: {
        marginLeft: '30%',
        gridArea: 'type1',
        width: '5vw',
        height: '5vw',
    },
    type2: {
        gridArea: 'type2',
        marginLeft: '30%',
        width: '5vw',
        height: '5vw',
    },
    type3: {
        gridArea: 'type3',
        marginLeft: '30%',
        width: '5vw',
        height: '5vw',
    },
    defenseAttack: {
        gridArea: 'defenseAttack',
        borderBottom: '3px solid rgb(54, 54, 54)',
    },
}));

let Pokemon = inject('Store')(
    observer(
        function PokeObject(props) {
            const classes = useStyles();

            const getManyPokemons = (count, clickedPage) => {
                if (props.Store.allPokemons)
                    console.log(clickedPage - 1);
                let Pokemons = [];
                for (let i = 0; i < count; i++) {
                    Pokemons[i] = props.Store.getPokemon((((clickedPage - 1) * count) + i));
                }
                return Pokemons;
            }
            const convertToBlock = (many_pokemons) => {
                let Div = [];
                many_pokemons.map((item, index) => {
                    return Div.push(((item === 0) || (item === undefined)) ? "" : <div className={classes.pokemon} onClick={() => modalWindow((props.Store.clickedPage - 1) * props.Store.countPokemonOnPage + index + 1)}>
                        <img className = {classes.imagePokemon} src={(item !== 0 && item !== undefined) ? item[0] : ""} alt='' /><br />
                        <p>{(item !== 0 && item !== undefined) ? item[1] : ""}</p>
                    </div>);
                })
                return Div;
            }
            const modalWindow = (index) => {
                console.log(index);
                props.Store.change_clickedPokemon(index);
                document.getElementById('modalElem').style.display = 'block';
                document.getElementById('poki').style.display = 'none';
            }
            let pokemonsOnPage = convertToBlock(getManyPokemons(props.Store.countPokemonOnPage, props.Store.clickedPage));
            let pokemon = props.Store.getPokemon(props.Store.clickedPokemon - 1);
            return (
                <div>
                    <div className={classes.modal + ' ' + classes.modalWindow} id='modalElem'>
                        <div id="close-block" onClick={() => {
                            document.getElementById('modalElem').style.display = 'none';
                            document.getElementById('poki').style.display = 'grid';
                        }}>
                            <span id="x">X</span>
                        </div>
                        <div className={classes.gridContainer}>
                            <div className = {classes.nameOfPokemon}>{(pokemon !== undefined) ? pokemon[1] : ""}</div>
                            <div className = {classes.photo}><img src={(pokemon !== undefined) ? pokemon[0] : ""} alt='' /></div>
                            <div className = {classes.stat}>Stat</div>
                            <div className = {classes.speed}>Speed: {(pokemon !== undefined) ? pokemon[2][0].base_stat : ""}</div>
                            <div className = {classes.specialStats}>Special defense: {(pokemon !== undefined) ? pokemon[2][1].base_stat : ""}<br />Special attack: {(pokemon !== undefined) ? pokemon[2][2].base_stat : ""}</div>
                            <div className = {classes.defenseAttack}>Defense: {(pokemon !== undefined) ? pokemon[2][3].base_stat : ""}<br />Attack: {(pokemon !== undefined) ? pokemon[2][4].base_stat : ""}</div>
                            <div className = {classes.hp}>HP: {(pokemon !== undefined) ? pokemon[2][5].base_stat : ""}</div>
                            <div className = {classes.type1 + ' ' + classes.typeOfPokemon} id={(pokemon !== undefined) ? pokemon[3][0].type.name : ""}>{(pokemon !== undefined) ? pokemon[3][0].type.name : ""}</div>
                            <div className = {classes.type2 + ' ' + classes.typeOfPokemon} id={(pokemon !== undefined) ? ((pokemon[3][1] === undefined) ? '' : pokemon[3][1].type.name) : ""}>{(pokemon !== undefined) ? ((pokemon[3][1] === undefined) ? '' : pokemon[3][1].type.name) : ""}</div>
                            <div className = {classes.type3 + ' ' + classes.typeOfPokemon} id={(pokemon !== undefined) ? ((pokemon[3][2] === undefined) ? '' : pokemon[3][2].type.name) : ""}>{(pokemon !== undefined) ? ((pokemon[3][2] === undefined) ? '' : pokemon[3][2].type.name) : ""}</div>
                        </div>
                    </div>
                    <div className = {classes.pokemonList} id='poki'>{pokemonsOnPage}</div>
                </div>
            );
        }
    )
)

export default Pokemon;