import React, { useEffect } from 'react';
import { inject, observer } from 'mobx-react';
import { makeStyles } from '@material-ui/core/styles';
// import { useMediaQuery } from '@material-ui/core/useMediaQuery';

import PokemonFile from './Pokemon';
import Pagination from './Pagination';
import Filter from './Filter';

const useStyles = makeStyles(theme => ({
    pageElements: {
        display: 'grid',
        gridTemplateColumns: '1fr',
        alignContent: 'center',
        textAlign: 'center'
    },
    sizeBox: {
        width: '10vw',
        margin: 'auto',
        outline: 'none'
    },
    pageName: {
        marginTop: '2vh',
        marginBottom: '1vh',
        fontSize: '40pt',
        color: 'rgb(54, 54, 54)'
    },
    justButton: {
        width: '70%',
        margin: 'auto',
        marginTop: '1vh',
        border: '2px solid rgb(54, 54, 54)',
        padding: '2px',
        "&:hover": {
            background: 'rgb(54, 54, 54)',
            color: 'white',
        }
    },
    searcher: {
        borderRadius: '20px',
        outline: 'none',
        width: '65%',
        border: '3px solid grey',
        paddingLeft: '5%',
        paddingTop: '2px',
        paddingBottom: '2px',
        '&:hover': {
            borderColor: 'rgb(54, 54, 54)',
        },
        '&:focus': {
            borderColor: 'white',
            background: 'white',
            color: 'black',
        },
        '&:focus:hover': {
            borderColor: 'white',
        },
    },
    buttons: {
        display: 'grid',
        width: '40vw',
        gridTemplate: 'repeat(4, 1fr) / repeat(1, minmax(50px , 1fr))',
        margin: 'auto',
        border: 'none',
        cursor: 'pointer',
        whiteSpace: 'nowrap'
    }
}));

let Page = inject('Store')(
    observer(
        function Page(props) {
            const classes = useStyles();

            useEffect(() =>{
                document.getElementById('filtr').className = classes.justButton;
                document.getElementById('body').style.background = 'rgb(209, 209, 209)';
                document.getElementById('body').style.userSelect = 'none';
            })



            const changeValue = (size) => {
                props.Store.change_countPokemonOnPage(size.target.value);
                props.Store.change_clickedPage(1);
            }
            const sortPokemons = () => {
                let pokemons = props.Store.allPokemons;
                console.log(pokemons);
                pokemons = pokemons.sort(sortFunction);
                props.Store.change_allPokemons(pokemons);
                console.log(pokemons);
                let temp = props.Store.countPokemonOnPage;
                props.Store.change_countPokemonOnPage(20);
                props.Store.change_countPokemonOnPage(temp);
            }
            const sortFunction = (a, b) => {
                if (a[1] > b[1]) {
                    return 1;
                }
                else if (a[1] < b[1]) {
                    return -1;
                }
                return 0;
            }
            const refresheAll = () => {
                props.Store.get_allpokemons();
                let types = props.Store.typesOfPokemons;
                for (let i = 0; i < types.length; i++) {
                    document.getElementById(types[i]).checked = false;
                }
                document.getElementById('field').value = '';
                props.Store.get_allpokemons();
            }
            const searchByValue = (val) => {
                document.getElementById('poki').style.display = "grid";
                document.getElementById('pagination').style.display = "grid";
                props.Store.change_clickedPage(1);
                let temp = require('../Store/poke.json');
                console.log(val.target.value.length);
                if (val.target.value.length === 0) {
                    props.Store.get_allpokemons();
                    return;
                }
                const result = temp.filter(pokemon => { if (pokemon[1].indexOf(val.target.value.toLowerCase()) !== -1) return true; return false; });
                if (result.length === 0) {
                    document.getElementById('poki').style.display = "none";
                    document.getElementById('pagination').style.display = "none";
                }
                props.Store.change_allPokemons(result);
            }
            return (
                <div className={classes.body}>
                    <div className={classes.pageElements}>
                        <h1 className={classes.pageName} onClick={refresheAll}>Pokedex</h1>
                        <select className={classes.sizeBox} onChange={changeValue}>
                            <option value="10">10</option>
                            <option value="20">20</option>
                            <option value="50">50</option>
                        </select>
                        <div className={classes.buttons}>
                            <input type='text' className={classes.justButton + ' ' + classes.searcher} id='field' name='q' placeholder="I will help you find" onChange={searchByValue} autocomplete="off" />
                            <div className={classes.justButton} onClick={sortPokemons}>Sort elements</div>
                            <div className={classes.justButton} onClick={refresheAll}>Refresh</div>
                            <Filter />
                        </div>
                        <PokemonFile />
                        <Pagination />
                    </div>
                </div>
            );
        }
    )
)

export default Page;