import React from 'react';
import { inject, observer } from 'mobx-react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    paginationMain: {
        margin: 'auto',
        marginTop: '2vh',
        marginBottom: '2vh',
        display: 'grid',
        gridTemplateColumns: 'repeat(9, 1fr)',
        textAlign: 'center',
        alignContent: 'center',
    },
    paginationValue: {
        background: 'rgb(241, 241, 241)',
        cursor: 'pointer',
        margin: '1vw',
        '&:hover':{
            background: 'rgb(126, 126, 126)',
        },
    },
    clickedPage: {
        background: 'rgb(54, 54, 54)',
        color: 'white',
        cursor: 'default',
        '&:hover': {

        },
    },
    dots: {
        paddingTop: '10px',
    },
}));

let Pagination = inject('Store')(
    observer(
        function Pagination(props) {
            const classes = useStyles();

            const getAllPages = () => {
                let allPages = [];
                for (let i = 1; i <= props.Store.allPokemons.length / props.Store.countPokemonOnPage; i++) {
                    allPages[i] = <div className={classes.paginationValue} id={i} onClick={() => changefunction(i)}>{i}</div>;
                }
                return allPages;
            }
            const changefunction = (value) => {
                console.log(value);
                props.Store.change_clickedPage(value);
            }
            const convertToNormalView = (many_pages) => {
                let normalView = [];
                let click = props.Store.clickedPage;
                if (many_pages.length <= 9) {
                    normalView = many_pages;
                    normalView[click] = <div className={classes.paginationValue + ' ' + classes.clickedPage} id={click} onClick={() => changefunction(click)}>{click}</div>;
                }
                else if (click < 6) {
                    for (let i = 1; i <= 7; i++) {
                        normalView[i] = many_pages[i];
                    }
                    normalView[8] = <div className={classes.dots}>...</div>
                    normalView[9] = many_pages[many_pages.length - 1];
                    normalView[click] = <div className={classes.paginationValue + ' ' + classes.clickedPage} id={click} onClick={() => changefunction(click)}>{click}</div>
                }
                else if (click > many_pages.length - 4) {
                    normalView[1] = many_pages[1];
                    normalView[2] = <div className={classes.dots}>...</div>
                    for (let i = 3, j = many_pages.length - 7; i <= 9; i++ , j++) {
                        normalView[i] = many_pages[j];
                    }
                    normalView[9 - ((many_pages.length - 1) - click)] = <div className={classes.paginationValue + ' ' + classes.clickedPage} id={click} onClick={() => changefunction(click)}>{click}</div>
                }
                else {
                    normalView[1] = many_pages[1];
                    normalView[2] = <div className={classes.dots}>...</div>
                    for (let i = 3, j = props.Store.clickedPage - 2; i <= 7; i++ , j++) {
                        normalView[i] = many_pages[j];
                    }
                    normalView[8] = <div className={classes.dots}>...</div>
                    normalView[9] = many_pages[many_pages.length - 1];
                    normalView[5] = <div className={classes.paginationValue + ' ' + classes.clickedPage} id={click} onClick={() => changefunction(click)}>{click}</div>
                }
                return normalView;
            }
            let pagination1 = convertToNormalView(getAllPages());
            return (
                <div className={classes.paginationMain} id='pagination'>{pagination1}</div>
            );
        }
    )
)

export default Pagination;