import React, {useEffect} from 'react';
import { inject, observer } from 'mobx-react';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles(theme => ({
    manyChecks: {
        display: 'grid',
        gridTemplate: '1fr / repeat(auto-fit, minmax(100px, 1fr))',
        marginTop: '5vh',
        marginLeft: '5vw',
        marginRight: '5vw',
    },
    modalFilterWindow: {
        width: '50vw',
        zIndex: '20',
        position: 'absolute',
        top: '10vh',
        left: '25vw',
        background: 'white',
        border: '2px solid silver',
        cursor: 'default',
        textAlign: 'left',
        display: 'none',
    },
    '#filter': {
        display: 'none',
    },
    closeBlock: {
        position: 'absolute',
        top: '1vh',
        right: '1vw',
        background: 'red',
        color: '#fff',
        fontFamily: "'Helvetica', 'Arial', sans-serif",
        fontSize: '2vw',
        fontWeight: 'bold',
        textAlign: 'center',
        width: '2.3vw',
        height: '2.3vw',
        borderRadius: '5px',
        cursor: 'pointer',
    },
    filterButtons: {
        width: '50%',
        display: 'grid',
        gridTemplate: "1fr / repeat(auto-fit, minmax(50px, 1fr))",
        margin: 'auto',
        marginBottom: '5vh',
        marginTop: '5vh',
    },
    button: {
        border: '2px solid silver',
        margin: 'auto',
        textAlign: 'center',
        display: 'grid',
        '&:first-child, &:last-child':{
            width: '80%',
        },
        '&:hover:first-child': {
            border: '2px solid rgb(66, 230, 51)',
            background: 'rgb(66, 230, 51)',
        },
        '&:hover:last-child': {
            border: '2px solid rgb(243, 77, 65)',
            background: 'rgb(243, 77, 65)',
        },
    },
    pokemonTypeCheckBox: {},
    filterBlock: {},
    x: {},
}));

let Filter = inject('Store')(
    observer(
        function FilterButton(props) {
            const classes = useStyles();

            useEffect(()=>{
                document.getElementById('close-block').className = classes.closeBlock;
            })

            const checks = () => {
                let types = props.Store.typesOfPokemons;
                let div = [];
                for (let i = 0; i < types.length; i++) {
                    div[i] = <div>
                        <input type="checkbox" value={types[i]} id={types[i]} className={classes.pokemonTypeCheckBox} />
                        <label for={types[i]}>{types[i]}</label>

                    </div>
                }
                return div;
            }

            const filterPokemons = () => {
                document.getElementById('poki').style.display = "grid";
                document.getElementById('pagination').style.display = "grid";
                props.Store.refreshFilter();
                let types = props.Store.typesOfPokemons;
                let arrChecked = [];
                for (let i = 0; i < types.length; i++) {
                    if (document.getElementById(types[i]).checked === true) {
                        arrChecked.push(types[i]);
                    }
                }
                console.log(arrChecked);
                document.getElementById('filter').style.display = 'none';

                let tempPokemons = [];
                for (let i = 0; i < props.Store.allPokemons.length; i++) {

                    let tempPoke = props.Store.allPokemons[i];
                    let isCorrect = false;
                    for (let j = 0; j < tempPoke[3].length; j++) {
                        let tempType = tempPoke[3][j].type.name;
                        for (let f = 0; f < arrChecked.length; f++) {
                            let filtredType = arrChecked[f];
                            if (tempType === filtredType) {
                                isCorrect = true;
                            }
                        }
                    }
                    if (isCorrect === true) {
                        tempPokemons.push(tempPoke);
                    }
                }
                if (tempPokemons.length === 0) {
                    document.getElementById('poki').style.display = "none";
                    document.getElementById('pagination').style.display = "none";
                }
                props.Store.change_allPokemons(tempPokemons);
            }
            let check = checks();
            return (
                <div className={classes.filterBlock}>
                    <div className={classes.modalFilterWindow} id={'filter'}>
                        <div className={classes.closeBlock} id='x' onClick={() => { document.getElementById('filter').style.display = 'none' }}>
                            <span className={classes.x}>X</span>
                        </div>
                        <div className={classes.manyChecks}>
                            {check}
                        </div>
                        <div className={classes.filterButtons}>
                            <div className={classes.button} onClick={filterPokemons}>Search</div>
                            <div className={classes.button} onClick={() => { document.getElementById('filter').style.display = 'none' }}>Cancel</div>
                        </div>
                    </div>
                    <div id='filtr' onClick={() => { document.getElementById('filter').style.display = 'block' }}>Filter</div>
                </div>
            );
        }
    )
)

export default Filter;